@import url(https://fonts.googleapis.com/css?family=Space+Mono:400,700);
h1 {
  margin: 0.5rem;
  margin-left: 0;
  font-size: 2rem; }

.nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden; }
  .nav ul li {
    float: left;
    font-weight: 700;
    text-decoration: none !important; }
    .nav ul li a {
      display: block;
      text-align: right;
      padding: 1rem;
      font-weight: 300;
      text-decoration: none;
      color: #ea148c !important; }
    .nav ul li a:hover {
      text-decoration: underline; }
    .nav ul li a.active {
      font-weight: 700;
      text-decoration: none !important; }

#intro {
  background-color: #111;
  color: #fff;
  width: 100%;
  margin-top: 1rem;
  padding-bottom: 3rem;
  text-align: center; }
  #intro img {
    width: 100%;
    padding: 2rem 0; }
    @media only screen and (min-width: 767px) {
      #intro img {
        padding: 6rem 3rem; } }

#intro-inverse {
  width: 100%;
  margin: 4rem auto;
  background-color: #fff;
  color: #111; }
  #intro-inverse p,
  #intro-inverse h3 {
    margin-bottom: 2rem; }

.article-info {
  margin: 1rem auto;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
  border-bottom: 3px dotted #111; }
  .article-info img {
    width: 100%;
    margin: 0 auto;
    display: block;
    -webkit-transition: 0.1s ease-in-out all;
    transition: 0.1s ease-in-out all;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border: 1px solid #fff;
    border-radius: 3px; }
  .article-info h4 {
    font-size: 1.125rem;
    margin-top: 0.5rem;
    font-weight: 700; }
  .article-info h4.secondary {
    margin-bottom: 0.25rem;
    margin-top: 0;
    font-weight: normal; }
  .article-info .name-and-icon {
    width: 100%;
    text-align: right; }
    .article-info .name-and-icon img {
      display: inline-block;
      padding-left: 1rem;
      height: 1.35rem;
      width: auto;
      -webkit-align-self: baseline;
              align-self: baseline; }
    .article-info .name-and-icon h5 {
      display: inline-block;
      font-weight: 400;
      font-size: 0.825rem;
      max-width: 80%; }
  .article-info p {
    font-size: 0.875rem;
    margin-top: 1rem;
    padding-bottom: 1rem;
    width: 100%; }
  .article-info a,
  .article-info a:visited {
    color: #ea148c;
    text-decoration: none; }
  .article-info a:hover {
    color: #ea148c;
    text-decoration: underline; }

.article-open {
  background: #ffffff;
  min-height: 80vw;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  padding: 0; }
  .article-open h2 {
    font-size: 1.75rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem; }
  .article-open img.article-photo {
    display: block;
    max-width: 100%;
    max-height: 80vh;
    height: auto;
    width: auto;
    margin: 0 auto;
    margin-bottom: 2rem !important;
    -webkit-transition: 0.1s ease-in-out all;
    transition: 0.1s ease-in-out all; }
  .article-open .icon-and-info {
    width: 100%;
    text-align: left;
    margin-left: 0.5rem; }
    .article-open .icon-and-info img {
      width: 1.5rem; }
    .article-open .icon-and-info h4 {
      margin-bottom: 1rem; }

@media only screen and (min-width: 767px) {
  .article-open {
    width: 100%;
    padding: 1rem; }
    .article-open .icon-and-info {
      text-align: right;
      margin-left: 20%; } }

.program {
  padding: 1rem;
  display: block;
  overflow: auto; }
  .program .filters {
    margin: 2rem auto;
    text-align: center; }
    @media only screen and (min-width: 767px) {
      .program .filters {
        margin: 4rem auto; } }
    .program .filters .buttons {
      display: inline-block; }
      .program .filters .buttons button {
        color: #fff;
        background-color: #111;
        font-size: 0.875rem;
        padding: 1rem 1.5rem;
        margin-bottom: 1rem;
        border-radius: 0.5rem;
        margin-right: 0.5rem; }
      .program .filters .buttons button:hover {
        background-color: #ea148c; }
      .program .filters .buttons button.is-checked {
        background-color: #ea148c;
        color: #fff; }

.my-masonry-grid {
  display: -webkit-flex;
  display: flex;
  margin-left: -2rem;
  width: auto; }

.my-masonry-grid_column {
  padding-left: 2rem;
  background-clip: padding-box; }

.about .large-text {
  color: #111 !important; }

.about h1,
.about h2 {
  margin-bottom: 2rem;
  margin-top: 2rem;
  color: #ea148c; }

.about a:link {
  text-decoration: none;
  color: #ea148c; }

.about a:hover {
  text-decoration: underline;
  color: #ea148c; }

.about ul {
  list-style-type: circle;
  padding-top: 0.5rem; }
  .about ul li {
    margin-left: 2rem;
    color: #111; }

footer {
  margin-top: 2rem;
  background: #111;
  color: #fff;
  padding: 2rem;
  padding-bottom: 0rem; }
  footer p {
    margin-top: 3rem;
    margin-bottom: 3rem; }
  footer a:link {
    color: #ea148c;
    text-decoration: underline; }

* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box; }

body {
  font-family: "Space Mono", monospace;
  font-size: 16px; }

p {
  font-family: "Space Mono", monospace; }

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0; }

.highlighted {
  color: #ea148c !important; }

a.pinklink {
  text-decoration: none;
  color: #ea148c; }

a.pinklink:hover {
  text-decoration: underline; }

